.MenuBtn {
  display: -ms-flexbox;
  display: flex;
  font-family: 'Roboto', sans-serif;
  color:  #ffffff;
  font-size: 1.125rem;
  font-weight: 300;
  letter-spacing: 1px;
  padding: 5px;
  cursor: pointer;
}
.BarsWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  margin-left: 15px;
}
.TopBar,
.BottomBar,
.MidleBar {
  position: relative;
  background-color:  #4ca9ff;
  width: 20px;
  height: 2px;
  opacity: 1;
  visibility: visible;
  -webkit-transition: all 200ms ease;
  -o-transition: all 200ms ease;
  transition: all 200ms ease;
}
.MidleBar {
  width: 16px;
  margin: 5px 0;
}
.Active.TopBar {
  -webkit-transform-origin: 0% 0%;
      -ms-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
  -webkit-transform: rotate(45deg);
      -ms-transform: rotate(45deg);
          transform: rotate(45deg);
  left: 0.5px;
}
.Active.MidleBar {
  opacity: 0;
  visibility: hidden;
}
.Active.BottomBar {
  -webkit-transform-origin: 0% 0%;
      -ms-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
  right: 0.5px;
}


