.ScrollBtn {
  position: absolute;
  left: 50%;
  bottom: 80px;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
}
.ScrollBtn span {
  display: block;
  width: 15px;
  height: 15px;
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
.Scroll1{
  border-bottom: 2px solid rgb(207, 210, 255);
  border-left: 2px solid rgb(207, 210, 255); 
  opacity: 0.3;
  -webkit-animation: changeColor 3s linear infinite;
          animation: changeColor 3s linear infinite; 
}
.Scroll2{
  border-bottom: 2px solid rgb(207, 210, 255);
  border-left: 2px solid rgb(207, 210, 255);
  opacity: 0.3;
  -webkit-animation: changeColor 3s linear infinite;
          animation: changeColor 3s linear infinite; 
  -webkit-animation-delay: 1s; 
          animation-delay: 1s;
}
.Scroll3{
  border-bottom: 2px solid rgb(207, 210, 255);
  border-left: 2px solid rgb(207, 210, 255);
  opacity: 0.3;
  -webkit-animation: changeColor 3s linear infinite;
          animation: changeColor 3s linear infinite; 
  -webkit-animation-delay: 2s; 
          animation-delay: 2s;
}
@-webkit-keyframes changeColor {
  0% {
    border-bottom: 2px solid rgb(207, 210, 255);
    border-left: 2px solid rgb(207, 210, 255);
  }
  33% {
    border-bottom: 2px solid rgb(0, 0, 0);
    border-left: 2px solid rgb(0, 0, 0);
  }
  66% {
    border-bottom: 2px solid rgb(235, 48, 77);
    border-left: 2px solid rgb(235, 48, 77);
  }
  100% {
    border-bottom: 2px solid rgb(207, 210, 255);
    border-left: 2px solid rgb(207, 210, 255);
  }
}
@keyframes changeColor {
  0% {
    border-bottom: 2px solid rgb(207, 210, 255);
    border-left: 2px solid rgb(207, 210, 255);
  }
  33% {
    border-bottom: 2px solid rgb(0, 0, 0);
    border-left: 2px solid rgb(0, 0, 0);
  }
  66% {
    border-bottom: 2px solid rgb(235, 48, 77);
    border-left: 2px solid rgb(235, 48, 77);
  }
  100% {
    border-bottom: 2px solid rgb(207, 210, 255);
    border-left: 2px solid rgb(207, 210, 255);
  }
}


